
import React, { useState } from 'react';
import './App.css';
// import About from './components/About';
import Navbar from './components/Navbar';
import Textform from './components/Textform';
import Alert from './components/Alert';

// import {
//   BrowserRouter as Router,
//   Routes,
//   Route,
// } from "react-router-dom";

function App() {


  const [alert, setAlert] = useState(null);
  const showMsg = (message, type) => {
    setAlert({
      msg: message,
      type: type
    }
    )
    setTimeout(() => {
      showMsg(null);
    }, 1000)
  }
  const [mystyle, setmystyle] = useState({
    backgroundColor: 'White',
    color: 'Black'
  })
  const [mode, setmode] = useState('light')
  const btn = () => {
    if (mode === 'light') {
      setmode('dark');
      setmystyle({
        backgroundColor: 'Black',
        color: 'White'
      });
      showMsg("dark mode enabled", "success");
      document.body.style.backgroundColor = 'black';
    }
    else {
      setmode('light');
      setmystyle({
        backgroundColor: 'White',
        color: 'Black'
      })
      showMsg("light mode enabled", "success");
      document.body.style.backgroundColor = 'White';
    }
  }
  return (
    <>
    {/* <Router> */}
      <Navbar title="website" about="about us" mode={mode} btn={btn} />
      <Alert alert={alert} />
      <Textform mystyle={mystyle}></Textform>
      <div className="container">
        {/* <Routes>
          <Route exact path="/About" element={ <About mystyle={mystyle}/>}>   
          </Route> */}
          {/* <Route exact path="/Textform" element={ <Textform mystyle={mystyle}/>}> */}
{/*             
          </Route>
        </Routes> */}
        </div>
      {/* </Router> */}
    </>
  );
}
export default App;
